<template>
  <div class="profile">
    <v-container v-if="username">
      <h1 class="ma-1">Perfil de utilizador de {{username}}</h1>
      <v-subheader>Nome</v-subheader>
      <p class="mx-5">{{name}}</p>
      <v-subheader>Email</v-subheader>
      <p class="mx-5">{{email}}</p>
      <v-subheader>Endereço</v-subheader>
      <p class="mx-5">{{address}}</p>
      <p class="mx-5">{{address2}}</p>
      <v-subheader>Localidade</v-subheader>
      <p class="mx-5">{{city}}</p>
      <v-subheader>Código-postal</v-subheader>
      <p class="mx-5">{{postalCode}}</p>
      <v-subheader v-if="cellphone">Telemóvel</v-subheader>
      <p class="mx-5" v-if="cellphone">{{cellphone}}</p>
      <v-subheader v-if="telephone">Telefone</v-subheader>
      <p class="mx-5" v-if="telephone">{{telephone}}</p>
      <v-subheader>Tipo de utilizador</v-subheader>
      <p class="mx-5">{{role}}</p>
      <v-subheader v-if="cc">CC</v-subheader>
      <p class="mx-5">{{cc}}</p>
      <v-subheader v-if="nif">NIF</v-subheader>
      <p class="mx-5" v-if="nif">{{nif}}</p>
      <v-subheader>Data da criação da conta de utilizador</v-subheader>
      <p class="mx-5">{{userCreationTime}}</p>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ProfileInformation',
  data () {
    return {
      username: null,
      name: null,
      email: null,
      address: null,
      city: null,
      postalCode: null,
      cellphone: null,
      telephone: null,
      address2: null,
      role: null,
      nif: null,
      cc: null,
      userCreationTime: null
    }
  },
  async mounted () {
    try {
      var token = JSON.parse(localStorage.getItem('login_token'))
      const response = await axios.post('/rest/profile', {
        username: token.username,
        token: token
      })
      console.log(response)
      var data = response.data
      this.username = token.username
      this.nif = data.user_nif
      this.address2 = data.user_address_extra
      this.address = data.user_address
      this.telephone = data.user_telephone
      this.city = data.user_address_city
      this.email = data.user_email
      this.postalCode = data.user_address_postal_code
      this.cellphone = data.user_cellphone
      this.name = data.user_name
      this.cc = data.user_cc
      this.role = data.user_role
      this.userCreationTime = data.user_creation_time
    } catch (error) {
      console.log(error)
    }
  },
  props: {
    source: String,
    isLogged: Boolean
  }
}
</script>



