<template>
  <div class="login">
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="500px">
        <v-btn flat color="white" slot="activator">Login</v-btn>
        <v-card class="elevation-12">
          <v-toolbar dark color="primary">
            <v-toolbar-title>Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-text-field
                prepend-icon="person"
                label="Username"
                v-model="username"
                :rules="nameRules"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="lock"
                label="Password"
                v-model="password"
                :rules="passwordRules"
                :append-icon="visibility ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (visibility = !visibility)"
                :type="visibility ? 'text' : 'password'"
                required
                @keyup.enter="submit"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="clear">Apagar</v-btn>
            <v-btn color="primary" @click="submit" :disabled="!valid">Login</v-btn>
          </v-card-actions>
        </v-card>
        <v-snackbar
          v-model="alert"
          :top="true"
          :multi-line="true"
          :color="'error'"
          :timeout="2000"
        >
          Username ou password errada
          <v-btn flat dark @click="alert = false">Close</v-btn>
        </v-snackbar>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Login',
  data () {
    return {
      dialog: false,
      valid: true,
      username: '',
      nameRules: [v => !!v || 'É necessário um username'],
      password: '',
      passwordRules: [v => !!v || 'É necessária uma password'],
      alert: false,
      visibility: false
    }
  },
  methods: {
    async submit () {
      if (this.$refs.form.validate()) {
        try {
          const response = await axios.post('/rest/login', {
            username: this.username,
            password: this.password
          })
          console.log(response)
          localStorage.setItem('login_token', JSON.stringify(response.data))
          this.$emit('logged-in', response.data)
          this.dialog = false
          this.clear()
        } catch (error) {
          console.log(error)
          this.alert = true
        }
      }
    },
    clear () {
      this.$refs.form.reset()
    }
  }
}
</script>



