<template>
  <div id="app">
    <v-app>
      <v-navigation-drawer
        fixed
        v-model="drawer"
        app
        clipped
      >
        <v-list>
          <v-subheader>
            Geral
          </v-subheader>
          <v-list-tile to="/">
            <v-list-tile-action>
              <v-icon>home</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>Página incial</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile v-if="isLogged" to="profile">
            <v-list-tile-action>
              <v-icon>account_circle</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>Perfil de utilizador</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar color="indigo" dark fixed app clipped-left>
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-toolbar-title>Agni</v-toolbar-title>
        <v-spacer></v-spacer>
        <register v-if="!isLogged"></register>
        <login v-on:logged-in="login($event)" v-if="!isLogged"></login>
        <v-btn flat color="white" v-if="isLogged" @click="logout">Logout</v-btn>
      </v-toolbar>
      <v-content>
        <router-view></router-view>
      </v-content>
      <v-footer color="indigo" app inset height="auto">
        <v-layout justify-center>
          <v-card
            flat
            tile
            class="indigo white--text text-xs-center"
          >
            <v-card-text class="white--text">
              &copy; {{ new Date().getFullYear() }} — <strong>Software Elementalists</strong>
            </v-card-text>
          </v-card>
        </v-layout>
      </v-footer>
    </v-app>
  </div>
</template>

<script>
import Login from '@/components/Login'
import Register from '@/components/Register'
import axios from 'axios'

export default {
  name: 'App',
  data () {
    return {
      drawer: false,
      isLogged: false,
      loginToken: null
    }
  },
  components: {
    Login,
    Register
  },
  mounted () {
    this.loginToken = JSON.parse(localStorage.getItem('login_token'))
    if (this.loginToken) {
      this.isLogged = true
    }
  },
  methods: {
    login (token) {
      this.loginToken = token
      this.isLogged = true
    },
    async logout () {
      try {
        const response = await axios.post('/rest/logout', {
          username: this.loginToken.username,
          tokenID: this.loginToken.tokenID,
          creationData: this.loginToken.creationData,
          expirationData: this.loginToken.expirationData
        })
        console.log(response)
        this.loginToken = null
        this.isLogged = false
        localStorage.removeItem('login_token')
        this.$router.push('/')
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>



