<template>
  <div class="register">
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="500px">
        <v-btn flat color="white" slot="activator">Registar</v-btn>
        <v-card class="elevation-12">
          <v-toolbar dark color="primary">
            <v-toolbar-title>Registar</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-text-field
                prepend-icon="person"
                label="Username"
                v-model="username"
                :rules="usernameRules"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="lock"
                label="Password"
                v-model="password"
                :rules="passwordRules"
                :append-icon="passwordVisibility ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (passwordVisibility = !passwordVisibility)"
                :type="passwordVisibility ? 'text' : 'password'"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="lock_outline"
                label="Confirmação"
                v-model="confirmation"
                :rules="confirmationRules"
                :append-icon="confirmationVisibility ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (confirmationVisibility = !confirmationVisibility)"
                :type="confirmationVisibility ? 'text' : 'password'"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="email"
                label="E-mail"
                v-model="email"
                :rules="emailRules"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="account_box"
                label="Nome completo"
                v-model="name"
                :rules="nameRules"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="home"
                label="Endereço do utilizador"
                v-model="address"
                :rules="addressRules"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                label="Endereço do utilizador (2ª linha)"
                v-model="address2"
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="location_city"
                label="Localidade do utilizador"
                v-model="city"
                :rules="cityRules"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="location_on"
                label="Código Postal do utilizador"
                v-model="postalCode"
                :mask="postalCodeMask"
                :rules="[
                  v => !!v || 'É necessário o código postal do utilizador',
                  v => v.length === 7 || 'O código postal tem 7 digitos'
                ]"
                required
                @keyup.enter="submit"
              ></v-text-field>
              <v-select
                prepend-icon="face"
                label="Tipo de utilizador"
                v-model="role"
                :items="roles"
                :rules="[v => !!v || 'É necessário escolher um tipo de utilizador']"
                required
              ></v-select>
              <v-text-field
                prepend-icon="smartphone"
                label="Telemóvel do utilizador"
                v-model.number="cellphone"
                :mask="cellphoneMask"
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="phone"
                label="Telefone do utilizador"
                v-model.number="telephone"
                :mask="telephoneMask"
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="credit_card"
                label="Cartão de Cidadão do utilizador"
                v-model="cc"
                :mask="ccMask"
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                prepend-icon="credit_card"
                label="NIF do utilizador"
                v-model.number="nif"
                :mask="nifMask"
                @keyup.enter="submit"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="clear">Clear</v-btn>
            <v-btn color="primary" @click="submit" :disabled="!valid">Registar</v-btn>
          </v-card-actions>
        </v-card>
        <v-snackbar
          v-model="alert"
          :top="true"
          :multi-line="true"
          :color="'error'"
          :timeout="2000"
        >
          Algo correu mal ao registar
          <v-btn flat dark @click="alert = false">Close</v-btn>
        </v-snackbar>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Login',
  data () {
    return {
      dialog: false,
      valid: true,
      username: '',
      usernameRules: [v => !!v || 'É necessário um username'],
      password: '',
      passwordRules: [v => !!v || 'É necessária uma password'],
      confirmation: '',
      confirmationRules: [
        v => !!v || 'É necessário confirmar a password',
        v => v === this.password || 'É necessário este campo ser igual à sua password'
      ],
      email: '',
      emailRules: [
        v => !!v || 'É necessário um email',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'O email tem de ser válido'
      ],
      name: '',
      nameRules: [v => !!v || 'É necessário o seu nome completo'],
      address: '',
      addressRules: [v => !!v || 'É necessário o seu endereço'],
      address2: '',
      city: '',
      cityRules: [v => !!v || 'É necessário a sua localidade'],
      postalCode: '',
      postalCodeMask: '####-###',
      role: 'USER',
      roles: [
        'USER',
        'GBO',
        'GS',
        'OBE'
      ],
      cellphone: null,
      cellphoneMask: '##-###-##-##',
      telephone: null,
      telephoneMask: '###-###-###',
      cc: null,
      ccMask: '########-N-NNN',
      nif: null,
      nifMask: '###-###-###',
      alert: false,
      passwordVisibility: false,
      confirmationVisibility: false
    }
  },
  props: {
    source: String,
    isLogged: Boolean
  },
  methods: {
    async submit () {
      if (this.$refs.form.validate()) {
        try {
          const response = await axios.post('/rest/register', {
            username: this.username,
            password: this.password,
            confirmation: this.confirmation,
            name: this.name,
            email: this.email,
            role: this.role,
            cellphone: this.cellphone,
            telephone: this.telephone,
            address: this.address,
            addressExtra: this.address2,
            addressCity: this.city,
            addressPostalCode: this.postalCode,
            nif: this.nif,
            cc: this.cc
          })
          console.log(response)
          this.dialog = false
          this.clear()
        } catch (error) {
          console.log(error)
          this.alert = true
        }
      }
    },
    clear () {
      this.$refs.form.reset()
    }
  }
}
</script>



